import React from "react";
import MakeupP from "./images/Makeup.jpg";
function Makeup() {
  return (
    <div>
      <div className="MakeupPhotos">
        <div class="container">
          <div class="column align-items-left my-5">
            <div class="row-lg-7">
              <img
                class="img-fluid rounded mb-4 mb-lg-0"
                src={MakeupP}
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
      <div className="Makeup">
        <div className="grid-container">
          <div class="row align-items-left my-5">
            <div class="grid-item col-lg-4">
              <p>
                <h5>Makeup - Application</h5>
                <ul>Price: $45</ul>
                <ul>Description: Makeup Application</ul>
              </p>

              <p>
                <h5>Makeup - Wedding</h5>
                <ul>Price: $50</ul>
                <ul>Description: Special makeup for weddings.</ul>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Makeup;
