import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Navigation, Footer, Home, Contact, Hair, Nails, EAB, Eyelashes, CPF, BodyWork, BWaBC, Whiten, Makeup } from "./components";
function App() {
  return (
    <div className="App">
      <Router>
        <Navigation />
        <Switch>
          <Route path="/" exact component={() => <Home />}/>
          <Route path="/contact" exact component={() => <Contact />}/>
          <Route path="/hair" exact component={() => <Hair />}/>
          <Route path="/nails" exact component={() => <Nails />}/>
          <Route path="/employees-and-bios" exact component={() => <EAB />}/>
          <Route path="/eyelashes" exact component={() => <Eyelashes />}/>
          <Route path="/chemical-peels-and-facials" exact component={() => <CPF />}/>
          <Route path="/body-work" exact component={() => <BodyWork />}/>
          <Route path="/body-waxing-and-brow-care" exact component={() => <BWaBC />}/>
          <Route path="/teeth-whitening" exact component={() => <Whiten />}/>
          <Route path="/makeup" exact component={() => <Makeup />}/>
          </Switch>
          <Footer />
          </Router>
    </div>
  );
}

export default App;