import React from "react";
import Colored from "./images/Multi-Colored-Hair.jpg";
function Home() {
  return (
    <div className="home">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              class="img-fluid rounded mb-4 mb-lg-0"
              src={Colored}
              alt="Multi-Colored Hair"
            />
          </div>
          <div class="col-lg-5">
            <h1 class="font-weight-light">Home</h1>
            <p>
              Specializing in hair extensions, hair colors and cuts, acrylic and
              gel nails with hand drawn art, pedicures, facial waxing, and mega-volume and classic eyelash
              extensions.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
