import React from "react";
import Lash from "./images/Eyelash.jfif";
import ColorLash from "./images/Colored Lashes.jfif";
function Eyelashes() {
  return (
    <div>
      <div className="eyelashPhotos">
        <div class="container">
          <div class="column align-items-left my-5">
            <div class="row-lg-7">
              <img class="img-fluid rounded mb-4 mb-lg-0" src={Lash} alt="" />
              <img
                class="img-fluid rounded mb-4 mb-lg-0"
                src={ColorLash}
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
      <h1 class="font-weight-light">Eyelash/Brow Extensions and Tinting</h1>
      <div className="eyelashOptions">
        <div className="grid-container">
          <div class="row align-items-left my-5">
            <div class="grid-item col-lg-5">
              <p>
                <h5>Eyelash Extension Re-Touch</h5>
                <ul>
                  Description: 90 minute re-touch which should be suitable for
                  2-3 week appointments for normal customers who maintain great
                  retention.
                </ul>
                <ul>Price: $65</ul>
              </p>

              <p>
                <h5>Eyelash Extension Hybrid Fill</h5>
                <ul>
                  Description: 90 minute re-touch which should be suitable for
                  2-3 week appointments for normal customers who maintain great
                  retention.
                </ul>
                <ul>Price: $65</ul>
              </p>

              <p>
                <h5>Eyelash Extension Volume Set</h5>
                <ul>
                  Description: Full set of Volume Handmade Fans! This lash set
                  will be 90-100% coverage of your natural lashes. Trust us, you
                  will be instagram ready!
                </ul>
                <ul>Price: $115</ul>
              </p>
            </div>
            <div class="grid-item col-lg-5">
              <p>
                <h5>Eyelash Extension Set</h5>
                <ul>
                  Description: Classic Eyelash extensions, full set amounting to
                  70-80% of your natural lashes begin lashed. This set is full
                  and fluffy!
                </ul>
                <ul>Price: $100</ul>
              </p>

              <p>
                <h5>Eyelash Extension Mega set</h5>
                <ul>
                  Description: Full set of Mega Volume Handmade Fans! This lash
                  set will be 90-100% coverage of your natural lashes. Trust us,
                  you will be instagram ready!
                </ul>
                <ul>Price: $130</ul>
              </p>

              <p>
                <h5>Lash Lift and Tint</h5>
                <ul>Description: Lash, Lift, and Tint</ul>
                <ul>Price: $55</ul>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Eyelashes;
