import React from "react";
import April from "./images/April.jpg";
import Tawnya from "./images/Tawnya.jpg";
import SaraP from "./images/SaraP.jpeg";
import Brekke from "./images/Brekke.jpg";
import Katie from "./images/Katie.jpeg";
import Maria from "./images/Maria.jpg";
import Jen from "./images/Jen.jpg";
import Kelsey from "./images/Kelsey.jpeg";
import Mariah from "./images/Mariah.jpeg";
import SaraG from "./images/SaraG.jpg";
import Veronica from "./images/Veronica.jpg";
import Jordyn from "./images/Jordyn.jpeg";
function EAB() {
  return (
    <div>
      <h1 class="font-weight-light">Employees and Bios</h1>
      <div className="employees-and-bios">
        <div class="grid-container">
          <div class="row align-items-left my-5">
            <div class="grid-item col-lg-4">
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Tawnya}
                  alt=""
                />
                <h5>Tawnya Pirnie</h5>
                <ul>
                  Bio: Hey ! I’m glad you are here! I’ve been a hairstylist
                  since 1995, and I still love it! I opened Ts Head to Nails in
                  2012, and have expanded a couple times until we settled in at
                  our current location. My favorite services to provide are
                  eyelash extensions, highlights, and acrylic nails! All my kids
                  are grown and I have a grand baby that I spoil as often as
                  possible (: Hope to see you soon!
                </ul>
              </p>

              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={SaraG}
                  alt=""
                />
                <h5>Sara Gatlin</h5>
                <ul>
                  Bio: I'm Sara, 31 dog mom :) been doing lashes since October
                  2020. Graduated The salon professional academy April 2021
                  Esthetics. I really want to get into sugaring and light
                  therapy treatment. Big things coming soon :)
                </ul>
              </p>
              <br />
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={April}
                  alt=""
                />
                <h5>April A</h5>
                <ul>
                  Bio: Hi my name is April and I'm a licensed esthetician. I'm
                  the owner of Radiat Glo Esthetics LLC inside T's Head to Nails
                  in Nampa. I'm certified in microneedling, dermaplanning,
                  enzyme and chemical peels and waxing. Im a mom to 14yr old
                  triplets in high school(fun). I'd love to work with you???
                </ul>
              </p>

              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={SaraP}
                  alt=""
                />
                <h5>Sara Pirnie</h5>
              </p>
              </div>
            <div class="grid-item col-lg-4">
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Brekke}
                  alt=""
                />
                <h5>Brekke Pokorney</h5>
                <ul>
                  Bio: Hi, I'm Brekke Pokorney (BRECK-KEY POE-CORN-KNEE) ya,
                  it's a hard one. I went to school at O.F.A. in Boise in 2013,
                  and been licensed and working since. I started out in a little
                  coffee house/tan spa doing gel and mostly pedicures, pedis are
                  still my favorite service! I've been here at T's since
                  December of 2014. I offer Pedicures, Acrylic Fills and Formed
                  Acrylic Full Sets, Gel Polish Manicures, and Hard Gel Overlays
                  on the natural nail. My goal is to keep your natural nail as
                  thick and healthy as possible while enhancing its beauty.
                  Aside from the Salon I am married and we have one perfect boy
                  born in 2011. As a family we enjoy dirt biking,
                  paddleboarding, jet skiing, skiing, traveling, and video
                  games. My personal hobbies are reading, painting, writing,
                  listening to any and all true crime podcasts, good food and
                  live music, even bad live music. I can be a little dark but
                  we'll never run out of things to talk about!
                </ul>
              </p>
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Katie}
                  alt=""
                />
                <h5>Katie Barrow</h5>

                <ul>
                  Bio: I’m a working wife and mama with 3 kiddos. Been in the
                  nail biz and working at Ts head to Nails for almost 3 years. I
                  am now also certified in teeth whitening. It’s a wonderful way
                  to brighten your smile and love yourself even more! I love
                  what I do, love where I work, and love my life.
                </ul>
              </p>

              <p>
                <img class="img-fluid rounded mb-4 mb-lg-0" src={Jen} alt="" />
                <h5>Jen Libengood</h5>
              </p>
            </div>
            <div class="grid-item col-lg-4">
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Mariah}
                  alt=""
                />
                <h5>Mariah Gonzales</h5>

                <ul>
                  Bio: Hello, my name is Mariah. I have an ever growing passion
                  for skin care, lashes, and beauty. Super blessed that I got
                  the opportunity to share everything that I have learned and
                  cannot wait to share my passion with you.
                </ul>
              </p>

              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Kelsey}
                  alt=""
                />
                <h5>Kelsey Kay (Rowles)</h5>
                <ul>
                  Bio: I’m Kelsey, AKA urlocalcolorfairie. I adore everything
                  weird and abnormal. I’m a fashion color, mullet, and color
                  correction enthusiast, but also ready to take an any hair
                  project you throw at me!
                </ul>
              </p>

              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Maria}
                  alt=""
                />
                <h5>Maria Cadenas</h5>
                <ul>
                  Bio: Love doing nails. Love the color purple. I enjoy learning
                  new things and experimenting with new styles.
                </ul>
              </p>

              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Veronica}
                  alt=""
                />
                <h5>Veronica Robinson</h5>
                <ul>
                  Bio: Nail Technician for 5 years and Nail Technician
                  Instructor for 3 years. Whether you would like a relaxing foot
                  massage or fun nail art, come stop in and I’ll get you taken
                  care of!
                </ul>
              </p>
              <p>
                <img
                  class="img-fluid rounded mb-4 mb-lg-0"
                  src={Jordyn}
                  alt=""
                />
                <h5>Jordyn Taylor</h5>
                <ul>
                  Bio: Hi! I’m Jordyn. I started lashing in January of 2021 and
                  fell in love with doing it. There’s no better feeling than
                  making someone feel good about themself! :) When I’m not
                  lashing you can find me working at the tanning salon or
                  spending time with family. I look forward to meeting you!
                </ul>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EAB;
