import React from "react";
import { Link, withRouter } from "react-router-dom";

function Navigation(props) {
  return (
    <div className="navigation">
      <nav class="navbar navbar-expand">
        <div class="container">
          <div class="siteTitle">
            <h5>T's Head to Nails Full Service Salon</h5>
          </div>
          <div class="siteLinks">
            <ul class="navbar-nav ml-auto">
              <li
                class={`nav-item ${
                  props.location.pathname === "/" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/">
                  Home
                  <span class="sr-only">(current)</span>
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/hair" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/hair">
                  Hair
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/Nails" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Nails">
                  Nails
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/Eyelashes" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Eyelashes">
                  Eyelashes
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/Chemical-Peels-and-Facials"
                    ? "active"
                    : ""
                }`}
              >
                <Link class="nav-link" to="/Chemical-Peels-and-Facials">
                  Chemical Peels and Facials
                </Link>
              </li>
             {/* <li
                class={`nav-item ${
                  props.location.pathname === "/Body-Work" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Body-Work">
                  Body Work
                </Link>
              </li> */}
              <li
                class={`nav-item ${
                  props.location.pathname === "/Body-Waxing-and-Brow-Care" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Body-Waxing-and-Brow-Care">
                  Body Waxing and Brow Care
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/Teeth-Whitening" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Teeth-Whitening">
                  Teeth Whitening
                </Link>
              </li>
              {/* <li
                class={`nav-item ${
                  props.location.pathname === "/Makeup" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/Makeup">
                  Makeup
                </Link>
              </li> */}
              <li
                class={`nav-item ${
                  props.location.pathname === "/Employees-and-Bios"
                    ? "active"
                    : ""
                }`}
              >
                <Link class="nav-link" to="/Employees-and-Bios">
                  Employees and Bios
                </Link>
              </li>
              <li
                class={`nav-item ${
                  props.location.pathname === "/contact" ? "active" : ""
                }`}
              >
                <Link class="nav-link" to="/contact">
                  Contact
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default withRouter(Navigation);
